    0% {
      opacity: 01%;
    }
    100% {
      opacity: 100%;
    }
} */

.main{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.section > div{
    /* animation: 2s ease-out fadeIn; */
    margin: 50px 0px;
    padding-top: 50px;
}

.home-footer{
  padding: 15px 0;
}

.home-footer a{
    color: #d3dcf1;
    transition: all 0.3s ease-out;
}

.home-footer a:hover{
  color: rgba(216, 216, 216, 0.959);
} 

.section-title{
  color: #f0f8ff;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 0;
}

.section-subtitle{
  color: #d3dcf1a9;
  margin-top: 10px;
  font-size: 1.1rem;
}
#intro-card{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-family: source-code-pro;
    font-weight: 500;
    -webkit-flex-wrap: wrap-reverse;
            flex-wrap: wrap-reverse;
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 100vh;
    margin-top: 0px;
    padding-top: 0px;
}

@media screen and (max-width: 900px) {
    #intro-card{
      margin-top: 100px;
    }
  }
  

#intro-card div{
    margin: 0 20px;
}

.intro-text{
    text-align:left;
    min-width:100px;
    max-width:500px;
    color: #d3dcf1;
}

.intro-text p{
    margin: .7em 0;
    font-size: 1.2rem;
}
.intro-text h1{
    font-size: 2.1em;
}
.intro-text h2{
    margin: .4em 0;
}
.line-two{
    display: inline;
    color: #f0f8ff;
}
#pieloaf{
    color: #d3dcf1a9
}

#profile-pic{
    max-width: 300px;
    border-radius: 5px;
    box-shadow: 15px 12px rgba(0, 0, 0, 0.294);
}
.highlight{
    color:#ffcf9d
}
.ext-links{
    display: inline;
    padding-left: 0;
}
.ext-links li{
    font-size: 2rem;
    display: inline;
    margin: 0 3px;
}

.ext-links li a, 
.ext-links li span
{
    color: #d3dcf1;
    transition: all 0.3s ease-out;
}

.ext-links li a:hover,
.ext-links li span:hover{
    color: rgba(174, 210, 214, 0.959);
}

#discord-tip{
    font-size: 1.1rem;
    box-shadow: 6px 5px rgba(0, 0, 0, 0.294);
}

.ext-links li span:hover{
    cursor: pointer;
}
#about{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.about-text{
    margin-left: 30px;
    margin-right: 30px;
    color: #d3dcf1;
    font-family: source-code-pro;
    font-weight: 400;
    font-size: 1rem;
    text-align: justify;    
    max-width: 500px;
}
.languages{
    margin-right: 30px;
    margin-left: 30px;
    display: -webkit-flex;
    display: flex;
    max-width: 250px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding-left: 0;
}
.languages li{
    display: inline;
    padding: 2px 10px;
    margin: 3px;
    background-color: #d3dcf1;
    color: #293a55;
    font-family: source-code-pro;
    font-weight: 500;
    border-radius: 15px;

}

.pc_icon{
    width: 250px;
}

.about-header{
    margin-bottom: 10px;
}
.about-body{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap-reverse;
            flex-wrap: wrap-reverse;
}
#projects{
   max-width: 1200px;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   -webkit-align-items: center;
           align-items: center;
}

.project-cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    padding-left: 0;
    margin: 10px;
}
#projects footer {
    margin: 0 20px;
    margin-top: auto;
    font-family: source-code-pro;
    font-weight: 400;
    color: #d3dcf1a9;
}

#projects footer a{
    color: #ffcf9d;
    text-decoration: underline;
    transition: all 0.2s ease-out;
}
#projects footer a:hover{
    color: #f0f8ff;
}

.show-more{
    cursor: pointer;
    color: #f0f8ff;
    padding: 7px;
    border-radius: 4px;
    border: solid #d3dcf1 2px;
    font-family: source-code-pro;
    font-weight: 300;
    transition: all 0.2s ease-in-out;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.show-more:hover{
    background-color: #98aee242;
}

.show-more a{
    color: #d3dcf1;
}
.card-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #1b20415b;
    margin: 5px 10px;
    padding: 20px;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    position: relative;
    color: #f0f8ff;
    cursor: pointer;
}
.card-container:hover{
    box-shadow: 5px 5.5px rgba(0, 0, 0, 0.294);
    color: #ffcf9d;
}

.card-header {
    font-family:source-code-pro;
    font-weight: 500;
    padding: 10px 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.card-header h2{
    margin: 0;
    text-align: left;
}
.card-header .text{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; 
    -webkit-align-items: flex-start; 
            align-items: flex-start;
    max-width: 250px
}

.card-links{
    padding-left: 0;
    margin-top: 7px;
}
.card-links li a:hover{
    color: rgba(174, 210, 214, 0.959);
}
.card-links li a{
    color: #d3dcf1;
    transition: all 0.3s ease-out;
}

.card-links li{
    font-size: 1.5rem;
    display: inline;
    margin: 0 3px;
}

.card-header img{
    width: 60px;
    height: 60px;
    border-radius: 3px;
}
.card-text{
    color: #d3dcf1;
    text-align: left;
    font-family:source-code-pro;
    font-weight: 500;
}
.card-container footer {
    margin-top: auto;
}

footer li{
    display: inline;
    float: left;
    color: #d3dcf1a9;
    font-family: source-code-pro;
    font-weight: 400;
    padding-right: 10px;
}

.tech-used{
    padding-left: 0;
}
