.menu-item{
    padding: 0 5px;
    font-size: 1rem;
    display: inline;
}
.menu-item a:hover{
    color: rgba(184, 233, 255, 0.959);
}
.menu-item a{
    color: #f0f8ff;
    transition: all 0.3s ease-out;
}
.mobile-menu{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #23304e;
    list-style: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 25px;
    margin-top: -10px;
}

.m-nav-dropdown{
    position: absolute;
    width: 100%;
    right:0px;
    margin-top: 12px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-right: auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    background-color: #23304e;
    padding: 12px 7px;
     box-shadow: rgba(0, 0, 0, 0.404) 0px 8px 10px -8px;
     height: auto;
    transition: all 1s ease;
}

.menu-item{
    font-size: 1.5rem;
}
